.upper {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(6, 1fr);
  /* border: 1px solid red; */
  margin: 15px;
  padding: 13px;
  /* background-color: aqua; */
  box-shadow: 1px 1px 1px 1px rgb(137, 136, 194);
  border-radius: 5px;
}
.upper:hover {
  transform: scale(1.02);
  cursor: pointer;
}
body {
  background-color: rgb(42, 26, 87);
}
